import React, {useState, useEffect} from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Volume from './Volume';
import Switch from '@material-ui/core/Switch';
import { PlayButton } from '../styles/container';


const Drum = () => {
    let [bank, setBank] = useState(false);
    let [power, setPower] = useState(true);
    let [display, setDisplay] = useState("Display");
    useEffect(() => {
        document.addEventListener("keydown", (e)=>{
            if(e.keyCode === 81){
                document.getElementById("tamb").click();
            }else if(e.keyCode===87){
                document.getElementById("chhl").click();
            }else if(e.keyCode===69){
                document.getElementById("ride").click();
            }else if(e.keyCode===65){
                document.getElementById("kick").click();
            }else if(e.keyCode===83){
                document.getElementById("snare").click();
            }else if(e.keyCode===68){
                document.getElementById("bongo").click();
            }else if(e.keyCode===90){
                document.getElementById("cabasa").click();
            }else if(e.keyCode===88){
                document.getElementById("china").click();
            }else if(e.keyCode===67){
                document.getElementById("clap").click();
            }
        });
    });
    const changeBank = () => {
        setBank(!bank);
    }
    const changePower = () => {
        setPower(!power);
        let audios = document.getElementsByTagName("audio");
        for (let i = 0 ; i<audios.length ; i++){
            let audio = audios[i];
            audio.pause();
            audio.currentTime= 0;
        }
    }
    const Toggle = {
        Q: () => {
            let audio = document.getElementById("Q");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Tamb");
            }else if (power === true && bank === true){
                setDisplay("Harp sound");
            }
        },
        W: () => {
            let audio = document.getElementById("W");
            audio.pause();
            audio.currentTime=0;
            audio.play(); 
            if(power === true && bank === false){
                setDisplay("Chhl");
            }else if (power === true && bank === true){
                setDisplay("SF Beat");
            }
        },
        E: () => {
            let audio = document.getElementById("E");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Ride");
            }else if (power === true && bank === true){
                setDisplay("Chow Guitar");
            }
        },
        A: () => {
            let audio = document.getElementById("A");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Kick");
            }else if (power === true && bank === true){
                setDisplay("Piano Loop");
            }
        },
        S: () => {
            let audio = document.getElementById("S");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Snare");
            }else if (power === true && bank === true){
                setDisplay("Lead Loop");
            }
        },
        D: () => {
            let audio = document.getElementById("D");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Bongo");
            }else if (power === true && bank === true){
                setDisplay("HO-Chi Lead");
            }
        },
        Z: () => {
            let audio = document.getElementById("Z");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Cabasa");
            }else if (power === true && bank === true){
                setDisplay("Line Guitar");
            }
        },
        X: () => {
            let audio = document.getElementById("X");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("China");
            }else if (power === true && bank === true){
                setDisplay("Guitar Loop");
            }
        },
        C: () => {
            let audio = document.getElementById("C");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Clap");
            }else if (power === true && bank === true){
                setDisplay("The Nights");
            }
        },
    }
    return(
        <React.Fragment>
        <Container>
            <Row className="text-center h-25">
                <Col>
                    <p>Power</p>
                    <Switch
                    checked={power}
                    onChange={changePower}
                    />
                </Col>
                <Col>
                    <p>Select your bank</p>
                    <Switch
                    onChange = {changeBank}
                    checked={bank}
                    />
                </Col>
                <Col>
                    <p>Volume</p>
                    <Volume/>
                </Col>
            </Row>
            <Row style={{marginTop:"2%"}} className="h-100 d-flex flex-row row justify-content-center align-items-center">
                <Container style={{position:"relative"}} className="w-75">
                    <Row>
                        <div
                        id = "tamb"
                        className="drum-pad"
                        onClick={Toggle.Q}
                        >
                            <audio
                            src={bank ? "/music/harp-and-female-voice-progression_131bpm_A_minor.wav" :"/drum_sounds/linn-TAMB.wav"}
                            id = "Q"
                            className="clip"
                            muted={!power}
                            />
                            Q
                        </div>
                        <div
                        className="drum-pad"
                        id="chhl"
                        onClick={Toggle.W}
                        >
                            W
                            <audio
                            src={bank ? "/music/SF_BeatD100-01.mp3" :"/drum_sounds/linn-CHHL.wav"}
                            id = "W"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="ride"
                        onClick={Toggle.E}
                        >
                            E
                            <audio
                            src={bank ? "/music/SF_ChowGuitar110C-03.mp3" :"/drum_sounds/linn-RIDE.wav"}
                            id = "E"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                    </Row>
                    <Row>
                        <div
                        className="drum-pad"
                        id="kick"
                        onClick={Toggle.A}
                        >
                            A
                            <audio
                            src={bank? "/music/PianoLp1 in Gm.mp3" : "/drum_sounds/linn-Kick.wav"}
                            id = "A"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="snare"
                        onClick={Toggle.S}
                        >
                            S
                            <audio
                            src={bank? "/music/Lead Lp1 in Am.mp3" :"/drum_sounds/linn-SNARE-2.wav"}
                            id = "S"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="bongo"
                        onClick={Toggle.D}
                        >
                            D
                            <audio
                            src={bank? "/music/HO_Chi Lead128E-05.mp3" :"/drum_sounds/linn-BONGO1.wav"}
                            id = "D"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                    </Row>
                    <Row>
                        <div
                        className="drum-pad"
                        id="cabasa"
                        onClick={Toggle.Z}
                        >
                            Z
                            <audio
                            src={bank? "/music/GuitarlineK02 94D-02.mp3" :"/drum_sounds/linn-CABASA.wav"}
                            id = "Z"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="china"
                        onClick={Toggle.X}
                        >
                            X
                            <audio
                            src={bank ? "/music/Guitar_loop32(160BPM).mp3" :"/drum_sounds/linn-CHINA1.wav"}
                            id = "X"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="clap"
                        onClick={Toggle.C}
                        >
                            C
                            <audio
                            src={bank? "/music/Avicii - The Nights (320  kbps).mp3" :"/drum_sounds/linn-CLAP.wav"}
                            id = "C"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                    </Row>
                </Container>
                <Container id="display" className="w-25 d-flex justify-content-center align-items-center">
                    {power ? display: "DrumPad is off!"}
                </Container>
            </Row>
        </Container>
        </React.Fragment>
    );
}



export default Drum;